<template>
    <v-container class="grey lighten-5">
        <v-row>
            <v-col>
                <v-card

                rounded="lg"
                class="mx-auto pa-12 pb-8"
                elevation="8"
                max-width="750"
                >
                    <br/><br/><br/><br/><br/>
                    <v-img
                        lazy-src="../../assets/home.jpg"
                        max-height="850"
                        max-width="540"
                        src="../../assets/home.jpg"
                        ></v-img>
                    <br/><br/><br/><br/><br/>
                </v-card>
            </v-col>
            <v-col>
              <div>
                <v-card
                  class="mx-auto pa-12 pb-8"
                  elevation="8"
                  max-width="448"
                  rounded="lg"
                >

                  <v-img
                      class="mt-8 mx-auto"
                      max-height="128"
                      max-width="128"
                      src="../../assets/usuario.png"
                      >
                  </v-img>
                  <div class="text-subtitle-1 text-medium-emphasis">Usuario:</div>

                  <v-text-field
                    v-model="account_email"
                    name="account_email"
                    density="compact"
                    placeholder="Email address"
                    prepend-inner-icon="mdi-email-outline"
                    variant="outlined"
                  ></v-text-field>

                  <div class="text-subtitle-1 text-medium-emphasis d-flex align-center justify-space-between">Contraseña</div>

                  <v-text-field
                    v-model="account_password"
                    name="account_password"
                    :append-inner-icon="visible ? 'mdi-eye-off' : 'mdi-eye'"
                    :type="visible ? 'text' : 'password'"
                    density="compact"
                    placeholder="Enter your password"
                    prepend-inner-icon="mdi-lock-outline"
                    variant="outlined"
                    @click:append-inner="visible = !visible"
                  ></v-text-field>
                  <v-card
                    class="mb-12"
                    color="surface-variant"
                    variant="tonal"
                  >
                  </v-card>
                  <v-btn
                    block
                    class="mb-8"
                    color="blue"
                    size="large"
                    variant="tonal"
                    @click="login"
                  >
                    Ingresar
                  </v-btn>
                  <v-card-text class="text-center">
                  </v-card-text>
                </v-card>
              </div>
            </v-col>
        </v-row>
    </v-container>
</template>
<script>
    export default {
        name: 'LoginUser',

        data: () => ({

            account_password: '',
            account_email: '',

            return: {

            },

        }),

        methods: {
                async login() {
                    const { account_email } = this;
                    const { account_password } = this;

				let url = 'http://200.110.153.252:8000/AppAdmin/api/V1/Login/'+account_email;

				let data = {account_password_form: account_password};

				const response = await fetch(url, {
						method: 'POST', // or 'PUT'
						body: JSON.stringify(data), // data can be `string` or {object}!
						headers:{
							'Content-Type': 'application/json'
						}
				})
				const myresponse = await response.json()

				if (myresponse == "YES"){
                        this.$router.push('/dashboard')// Redirecting to other page.
						return false;
				}
        else{
						alert ("Login error");
				}
        },
    },

  }
</script>
