<template>
    <v-data-table
        :headers="headers"
        :items="usuarios"
        :items-per-page="10"
        :search="search"
        class="elevation-1"
      >
        <template v-slot:top>
          <v-toolbar
            flat
          >
            <v-toolbar-title><v-icon>mdi-view-list</v-icon> - Listado de Clientes</v-toolbar-title>
              <v-divider
                class="mx-4"
                inset
                vertical
              ></v-divider>
              <v-spacer></v-spacer>
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Buscar"
                single-line
                hide-details
              ></v-text-field>
          </v-toolbar>
        </template>
                    
        <template v-slot:item.actions="{ item }">
          <v-icon
            small
            class="mr-2"
            color="green"
            @click="EditCustomer(item.cust_cod)"
          >
            mdi-pencil
          </v-icon>
          <v-icon
            small
            color="red"
            @click="DeleteCustomer(item.cust_cod)"
          >
            mdi-delete
          </v-icon>
        </template>
    </v-data-table>
</template>
<script>
import axios from 'axios';
  export default {
    name: 'TableCustomer',

    data () {
      return {

        dialog: false,
        putid: '',
        putdescription: '',
        putaddress: '',

        search: '',
        headers: [
          { text: 'Codigo Cliente', value: 'cust_cod' },
          {
            text: 'Cliente',
            align: 'start',
            sortable: false,
            value: 'cust_description',
          },
          { text: 'Direccion', value: 'cust_address' },
          { text: 'Acccion', value: 'actions', sortable: false },
        ],

        usuarios: [],

      }

    },

    methods: {
      async GetCustomer(){
          let url = "http://200.110.153.252:8000/jelevadores/api/V1/Customer";
          let response =  await axios.get(url)
          this.usuarios = response.data

      },

      async PutCustomer(){
          const {putid} = this;
          let url = 'http://200.110.153.252:8000/jelevadores/api/V1/Customer/'+putid;
          const {putdescription} = this;
          const {putaddress} = this;


          let data = {cust_description: putdescription, cust_address: putaddress};

					const response = await fetch(url, {
							method: 'PUT', // or 'PUT'
							body: JSON.stringify(data), // data can be `string` or {object}!
							headers:{
								'Content-Type': 'application/json'
							}
					})
					const myresponse = await response.json()

					if (myresponse.status == "Deleted"){
							alert ("Cliente Actualizado");
							location.reload();
							return false;
					}
					else{
							alert ("Error 500");
					}

      },

      async DeleteCustomer(cust_cod){
          let id = cust_cod;
          alert (id);
					let url = 'http://200.110.153.252:8000/jelevadores/api/V1/Customer/'+id;
					let data = {};

					const response = await fetch(url, {
							method: 'DELETE', // or 'PUT'
							body: JSON.stringify(data), // data can be `string` or {object}!
							headers:{
								'Content-Type': 'application/json'
							}
					})
					const myresponse = await response.json()

					if (myresponse.status == "Deleted"){
							alert ("Cliente Eliminado");
              location.reload();
							return false;
					}
					else{
							alert ("Error 500");
					}
      }

    },

    created(){

      this.GetCustomer();

    }
  }
</script>
