      search3: '',
      headers3: [
        { text: 'Codigo Inspecion', value: 'call_id' },
        {
          text: 'Cliente',
          align: 'start',
          sortable: false,
          value: 'call_customer',
        },
        { text: 'Fecha', value: 'call_date' },
        { text: 'Acccion', value: 'actions3', sortable: false },
      ],

      usuarios3: [],

      // Variables Data Table Stair
      search2: '',
      headers2: [
        { text: 'Codigo Inspecion', value: 'stair_id' },
