      // --  Metodos Call 
      async GetCall(){
          let url = "http://200.110.153.252:8000/jelevadores/api/V1/FormCall";
          let response =  await axios.get(url)
          this.usuarios3 = response.data

      },

      async DeleteCall(call_id){
          let id = call_id;
					let url = 'http://200.110.153.252:8000/jelevadores/api/V1/FormCall/'+id;
					let data = {};

					const response = await fetch(url, {
							method: 'PUT', // or 'PUT'
							body: JSON.stringify(data), // data can be `string` or {object}!
							headers:{
								'Content-Type': 'application/json'
							}
					})
					const myresponse = await response.json()

					if (myresponse.status == "Deleted"){
							alert ("Reporte Eliminado");
              location.reload();
							return false;
					}
					else{
							alert ("Error 500");
					}
      },

      async PDFCall(call_id){
          let id = call_id;
					let url = 'http://200.110.153.252:8000/jelevadores/api/V1/PDFFormCall/'+id;
					let data = {};

					const response = await fetch(url, {
							method: 'GET', // or 'PUT'
							body: JSON.stringify(data), // data can be `string` or {object}!
							headers:{
								'Content-Type': 'application/json'
							}
					})
					const myresponse = await response.json()

					if (myresponse.status == "PDF-CREADO"){
							alert ("Informe Creado");
              location.reload();
							return false;
					}
					else{
							alert ("Error 500");
					}
      },


