<template>
  <v-data-table
      :headers="headers"
      :items="usuarios"
      :items-per-page="10"
      :search="search"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar
          flat
        >
          <v-toolbar-title><v-icon>mdi-view-list</v-icon> - Listado de Tecnicos Elevadores</v-toolbar-title>
            <v-divider
              class="mx-4"
              inset
              vertical
            ></v-divider>
            <v-spacer></v-spacer>
            <v-text-field
              v-model="search"
              append-icon="mdi-magnify"
              label="Buscar"
              single-line
              hide-details
            ></v-text-field>
        </v-toolbar>
      </template>
    
      <template v-slot:item.actions="{ item }">
        <v-icon
          small
          class="mr-2"
          color="green"
          @click="EditTechnical(item.account_id)"
        >
          mdi-pencil
        </v-icon>
        <v-icon
          small
          color="red"
          @click="DeleteTechnical(item.account_id)"
        >
          mdi-delete
        </v-icon>
      </template>
  </v-data-table>
</template>
<script>
import axios from 'axios';
  export default {
    name: 'TableTechnical',

    data () {
      return {

        dialog: false,
        putid: '',
        putdescription: '',
        putaddress: '',

        search: '',
        headers: [
          { text: 'Codigo Cuenta', value: 'account_id' },
          {
            text: 'Correo',
            align: 'start',
            sortable: false,
            value: 'account_email',
          },
          { text: 'Nombre', value: 'account_name' },
          { text: 'Apellido', value: 'account_last_name' },
          { text: 'Acccion', value: 'actions', sortable: false },
        ],

        usuarios: [],

      }

    },

    methods: {
      async GetTechnical(){
          let url = "http://200.110.153.252:8000/AppAdmin/api/V1/Account";
          let response =  await axios.get(url)
          this.usuarios = response.data

      },

      async DeleteTechnical(account_id){
          let id = account_id;
          alert (id);
					let url = 'http://200.110.153.252:8000/AppAdmin/api/V1/Account'+id;
					let data = {};

					const response = await fetch(url, {
							method: 'DELETE', // or 'PUT'
							body: JSON.stringify(data), // data can be `string` or {object}!
							headers:{
								'Content-Type': 'application/json'
							}
					})
					const myresponse = await response.json()

					if (myresponse.status == "Deleted"){
							alert ("Tecnico de Elevadores Eliminado");
              location.reload();
							return false;
					}
					else{
							alert ("Error 500");
					}
      }

    },

    created(){

      this.GetTechnical();

    }
  }
</script>

