<template>
  <v-form
    ref="form"
  >
    <v-container>
      <v-card-title>Mantenimiento de Soporte Tecnico</v-card-title>
        <v-row>
          <v-col
            cols="12"
            md="5"
          >
            <v-text-field
                v-model="form_technical_name"
                solo
                label="Nombre"
                required
            ></v-text-field>
          </v-col>
          <v-col
            cols="12"
            md="5"
          >
            <v-text-field
                v-model="form_technical_last_name"
                solo
                label="Apellido"
                required
            ></v-text-field>
          </v-col>     
        </v-row>
        <v-row>
          <v-col
            cols="12"
            md="5"
          >
            <v-text-field
                v-model="form_technical_email"
                solo
                label="Correo"
                required
            ></v-text-field>
          </v-col>
          <v-col
            cols="12"
            md="2"
          >
            <v-btn
                color="success"
                class="mr-4"
                @click="AddTechnical"
            >
              Guardar
            </v-btn>
          </v-col>
        </v-row>
    </v-container>
  </v-form>
</template>
<script>
  export default {
    name: 'FormTechnical',

    data: () => ({
 
      name: '',
      
      
      
    }),

    methods: {
      async AddTechnical(){
		
					let url = 'http://200.110.153.252:8000/AppAdmin/api/V1/Account';
          const {form_technical_name} = this;
          const {form_technical_last_name} = this;
          const {form_technical_email} = this;

          
          let data = {account_name: form_technical_name, account_last_name: form_technical_last_name, account_email: form_technical_email};

					const response = await fetch(url, {
							method: 'POST', // or 'PUT'
							body: JSON.stringify(data), // data can be `string` or {object}!
							headers:{
								'Content-Type': 'application/json'
							}
					})  
					const myresponse = await response.json()

					if (myresponse.status == "Create"){
							alert ("Tecnico Elevadores Creado");
							location.reload();
							return false;
					}
					else{
							alert ("Error 500");
					}

				}
    },
  }
</script>