import Vue from 'vue'
import VueRouter from 'vue-router'

import Home from "./views/Home.vue";
import Dashboard from "./views/Dashboard.vue";
import Customer from "./views/DashboardCustomer.vue";
import TechnicalSupport from "./views/DashboardTechnicalSupport.vue";
import Elevators from "./views/DashboardElevators.vue";
import Stairs from "./views/DashboardStairs.vue";
import Call from "./views/DashboardCall.vue";


Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: "/customer",
    name: "customer",
    component: Customer
  },
  {
    path: "/technicalsupport",
    name: "technicalsupport",
    component: TechnicalSupport
  },
  {
    path: "/dashboard",
    name: "dashboard",
    component: Dashboard
  },
  {
    path: "/1",
    name: "elevators",
    component: Elevators
  },
  {
    path: "/2",
    name: "stairs",
    component: Stairs
  },
  {
    path: "/3",
    name: "call",
    component: Call
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
