<template>
  <v-form
    ref="form"
  >
    <v-container>
      <v-card-title>Mantenimiento de Clientes</v-card-title>
        <v-row>
          <v-col
            cols="12"
            md="5"
          >
            <v-text-field
              v-model="form_customer_name"
              solo
              label="Nombre"
              required
            ></v-text-field>
          </v-col>
          <v-col
            cols="12"
            md="5"
          >
            <v-text-field
              v-model="form_customer_address"
              solo
              label="Direccion"
              required
            ></v-text-field>
          </v-col>
          <v-col
            cols="12"
            md="2"
          >
            <v-btn
              color="success"
              class="mr-4"
              @click="AddCustomer"
            >
              Guardar
            </v-btn>
          </v-col>
        </v-row>
    </v-container>
  </v-form>
</template>
<script>
  export default {
    name: 'FormAddCustomer',

    data: () => ({

      form_customer_name: '',
      form_customer_address: '',

    }),

    methods: {
      async AddCustomer(){

					let url = 'http://200.110.153.252:8000/jelevadores/api/V1/Customer';
          const {form_customer_name} = this;
          const {form_customer_address} = this;

          let data = {cust_description: form_customer_name, cust_address: form_customer_address};

					const response = await fetch(url, {
							method: 'POST', // or 'PUT'
							body: JSON.stringify(data), // data can be `string` or {object}!
							headers:{
								'Content-Type': 'application/json'
							}
					})
					const myresponse = await response.json()

					if (myresponse.status == "Create"){
							alert ("Cliente Creado");
							location.reload();
							return false;
					}
					else{
							alert ("Error 500");
					}

				}
    },
  }
</script>
